.Methods {
	display: flex;
	flex-direction: column;
	width: 100%;
	height: 100%;
}

.Row {
	display: flex;
	flex-direction: row;
	height: 50%;
}
.Methods .Row a {
	display: flex;
	width: 50%;
	height: 100%;
	cursor: pointer;
}

.Methods .Row a svg {
	width: 100%;
	height: 100%;
}

.Disabled,
.Disabled:hover {
	cursor: not-allowed;
	pointer-events: none;
}
